import React, { Component } from "react";
import { Link } from "react-router-dom";
import logo from "../img/logo192.png";
import withFirebaseAuth from "react-with-firebase-auth";
import * as firebase from "firebase/app";
import "firebase/auth";
import firebaseConfig from "../Firebase.js";
import NombreFoto from "./NombreFoto";

const firebaseApp = firebase.initializeApp(firebaseConfig);

class Navbar extends Component {
  render() {
    const { user, signOut, signInWithGoogle } = this.props;
    return (
      <div className="col-2 p-0">
        <nav
          className="p-0 navbar navbar-dark bg-primary align-items-start"
          style={{ minHeight: "100vh" }}
        >
          <ul className="navbar-nav flex-column mx-auto">
            <li className="nav-item">
              <Link className="navbar-brand mx-auto p-0" to={process.env.PUBLIC_URL+"/inicio"}>
                <div
                  style={{
                    borderBottom: "1px solid rgba(255,255,255,0.3)",
                    marginBottom: "15px",
                  }}
                >
                  <div className="text-center mt-5">
                    <img
                      src={logo}
                      alt="logo_multifolio"
                      style={{ display: "inline-block" }}
                    />
                  </div>
                  <h1 style={{ textAlign: "center" }}>MULTIFOLIO</h1>
                </div>
              </Link>
            </li>

            {user ? (
              <div className="dropdown">
                <button
                  className="btn btn-primary d-flex justify-content-center dropdown-toggle align-items-center p-2 mx-auto"
                  data-toggle="dropdown"
                  type="button"
                  style={{lineHeight: "45px"}}
                >
                  <NombreFoto nombre={user.displayName} photo={user.photoURL} />
                </button>
                <div className="dropdown-menu" style={{fontSize: "1.2em"}}>
                  <Link className="dropdown-item" to={process.env.PUBLIC_URL+"/mi_perfil"}>
                    Mi perfil
                  </Link>
                  <Link className="dropdown-item" to={process.env.PUBLIC_URL+"/mis_proyectos"}>
                    Mis proyectos
                  </Link>
                  <div className="dropdown-item" onClick={signOut} style={{cursor: "pointer"}}>
                    Cerrar sesión
                  </div>
                </div>
              </div>
            ) : (
              <div></div>
            )}

            <li className="text-center">
              {user ? (
                <div></div>
              ) : (
                <button
                  type="button"
                  className="btn btn-success btn-lg my-2 mb-5"
                  onClick={signInWithGoogle}
                >
                  Iniciar sesión
                </button>
              )}
            </li>
            <li className="nav-item mt-2">
              <Link className="nav-link" to={process.env.PUBLIC_URL+"/inicio"}>
                <h4>Inicio</h4>
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to={process.env.PUBLIC_URL+"/categorias"}>
                <h4>Categorias</h4>
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to={process.env.PUBLIC_URL+"/buscar"}>
                <h4>Buscar</h4>
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to={process.env.PUBLIC_URL+"/acerca_de"}>
                <h4>Acerca de...</h4>
              </Link>
            </li>
          </ul>
        </nav>
      </div>
    );
  }
}
const firebaseAppAuth = firebaseApp.auth();
const providers = {
  googleProvider: new firebase.auth.GoogleAuthProvider(),
};

export default withFirebaseAuth({
  providers,
  firebaseAppAuth,
})(Navbar);
