import React from "react";
import Bienvenida from "./components/pages/Bienvenida.js";
import Categorias from "./components/pages/Categorias.js";
import Navbar from "./components/Navbar.js";
import "react-pro-sidebar/dist/css/styles.css";
import { BrowserRouter, Redirect, Route } from "react-router-dom";

function App() {
  return (
    <BrowserRouter>
      <div className="container-fluid">
        <div className="row">
          <Navbar />
          <div className="col-10">
            
            <Route exact path={process.env.PUBLIC_URL+"/inicio"} component={Bienvenida}/>
            <Route exact path={process.env.PUBLIC_URL+"/categorias"} component={Categorias} />
          </div>
        </div>
      </div>
    </BrowserRouter>
  );
}

export default App;
